
import React from 'react';
import { FormControl, InputLabel, Select, MenuItem, TextField, Button, Box } from '@mui/material';
import logo from "./logo1.gif";


const AppContent = ({
                        selectedPaper, setSelectedPaper, paperTypes, selectedType, setSelectedType, selectedWeight, setSelectedWeight,
                        startWidth, setStartWidth, startLength, setStartLength, finishWidth, setFinishWidth, finishLength, setFinishLength,
                        requiredSheets, setRequiredSheets, calculateResult, resultInches
                    }) => {
    return (
        <div className="App">
            <header className="App-header">

                <img src={logo} alt="logo" style={{ marginBottom: '20px' }} />

                <div className='menu'>
                    <FormControl fullWidth margin="normal">
                        <InputLabel id="paper-select-label">Name of the Paper</InputLabel>
                        <Select
                            labelId="paper-select-label"
                            value={selectedPaper}
                            onChange={e => setSelectedPaper(e.target.value)}
                            label="Name of the Paper"
                        >
                            {paperTypes.map((paper) => (
                                <MenuItem key={paper.name} value={paper.name}>{paper.name}</MenuItem>
                            ))}
                        </Select>
                    </FormControl>

                    <FormControl fullWidth margin="normal">
                        <InputLabel id="type-select-label">Type of Paper</InputLabel>
                        <Select
                            labelId="type-select-label"
                            value={selectedType}
                            onChange={e => setSelectedType(e.target.value)}
                            disabled={!selectedPaper}
                            label="Type of Paper"
                        >
                            {selectedPaper && paperTypes.find(paper => paper.name === selectedPaper)?.details.map(detail => (
                                <MenuItem key={detail.type} value={detail.type}>{detail.type}</MenuItem>
                            ))}
                        </Select>
                    </FormControl>

                    <FormControl fullWidth margin="normal">
                        <InputLabel id="weight-select-label">Weight of the Paper</InputLabel>
                        <Select
                            labelId="weight-select-label"
                            value={selectedWeight}
                            onChange={e => setSelectedWeight(e.target.value)}
                            disabled={!selectedType}
                            label="Weight of the Paper"
                        >
                            {selectedType && paperTypes.find(paper => paper.name === selectedPaper)?.details.find(detail => detail.type === selectedType)?.weights.map(weight => (
                                <MenuItem key={weight.value} value={weight.value}>{weight.value}</MenuItem>
                            ))}
                        </Select>
                    </FormControl>

                    {/* Dimensions input */}
                    <div className='menu2'>
                        <Box display="flex" justifyContent="space-between" marginTop={2}>
                            <div className='menu3'>
                                <TextField fullWidth label="Start Width (inches)" type="number" value={startWidth}
                                           onChange={e => setStartWidth(e.target.value)} margin="normal"/>
                            </div>

                            <div className='menu3'>
                                <TextField fullWidth label="Start Length (inches)" type="number" value={startLength}
                                           onChange={e => setStartLength(e.target.value)} margin="normal"/>
                            </div>
                        </Box>
                    </div>

                    <div className='menu2'>
                        <Box display="flex" justifyContent="space-between" marginTop={2}>
                            <div className='menu3'>
                                <TextField fullWidth label="Finish Width (inches)" type="number" value={finishWidth}
                                           onChange={e => setFinishWidth(e.target.value)} margin="normal"/>
                            </div>
                            <div className='menu3'>
                                <TextField fullWidth label="Finish Length (inches)" type="number" value={finishLength}
                                           onChange={e => setFinishLength(e.target.value)} margin="normal"/>
                            </div>
                        </Box>

                    </div>


                    <TextField fullWidth label="Required number of sheets" type="number" value={requiredSheets}
                               onChange={e => setRequiredSheets(e.target.value)} margin="normal"/>
                    <div className='button1'>
                        <Button variant="contained" onClick={calculateResult} style={{marginTop: '16px'}}>Calculate</Button>
                    </div>

                    <div className='resul'>
                        <div>{resultInches}</div>
                    </div>

                </div>
            </header>
        </div>
    );
};
export default AppContent;